import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;

function generateReport(labRequest, currentUser) {
  const documentDefinition = {
    content: [],
    styles: {
      header: {
        fontSize: 18,
        bold: true
      }
    }
  };
  generateHeader(labRequest, documentDefinition, currentUser);
  generatePatientInfo(labRequest, documentDefinition);
  generateExamsInfo(labRequest, documentDefinition);
  pdfMake
    .createPdf(documentDefinition)
    .download(
      `${labRequest.patient.first_name}-${labRequest.patient.last_name}-Report.pdf`
    );
}

function generateHeader(labRequest, documentDefinition, currentUser) {
  documentDefinition.content.push({
    columns: [
      {
        width: 140,
        image:
          "data:image/png;base64,"
      },
      {
        columns: [
          { width: "*", text: "" },
          {
            width: "auto",
            layout: "noBorders",
            table: {
              headerRows: 0,
              widths: ["*"],

              body: [
                [{ text: currentUser.laboratory.name, bold: true }],
                [{ text: currentUser.laboratory.email, bold: true }],
                [
                  {
                    text: `Tel: ${currentUser.laboratory.telephone}`,
                    bold: true
                  }
                ],
                [
                  {
                    text: `${currentUser.laboratory.location.city}, ${currentUser.laboratory.location.country}`,
                    bold: true
                  }
                ]
              ]
            }
          }
        ]
      }
    ]
  });
}

function generatePatientInfo(labRequest, documentDefinition) {
  documentDefinition.content.push({ text: "Patient:", style: "header" });
  documentDefinition.content.push({
    layout: "noBorders",
    table: {
      headerRows: 0,
      widths: ["auto", "*"],

      body: [
        [
          { text: "Name:", bold: true },
          labRequest.patient.first_name + " " + labRequest.patient.last_name
        ],
        [{ text: "Email:", bold: true }, labRequest.patient.email],
        [{ text: "Telephone:", bold: true }, labRequest.patient.telephone],
        [{ text: "Sex:", bold: true }, labRequest.patient.sex],
        [{ text: "Age:", bold: true }, labRequest.patient.age]
      ]
    }
  });
}

function generateExamsInfo(labRequest, documentDefinition) {
  labRequest.exams.forEach(exam => {
    generateExamInfo(exam, documentDefinition);
  });
}

function generateExamInfo(exam, documentDefinition) {
  documentDefinition.content.push({
    text: exam.name,
    style: "header",
    margin: [0, 20, 0, 0]
  });
  let examTable = {
    layout: "lightHorizontalLines",
    table: {
      headerRows: 1,
      widths: ["*", "*", "*"],

      body: [["", { text: "Value", bold: true }, { text: "Unit", bold: true }]]
    }
  };
  exam.parameters.forEach(parameter => {
    examTable.table.body.push([
      parameter.name,
      parameter.value,
      parameter.unit
    ]);
  });
  documentDefinition.content.push(examTable);
}

export { generateReport };
