<template>
  <div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Name</th>
          <th>Aliases</th>
          <th>Department</th>
          <th>Price</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr :exam="exam" v-for="(exam, index) in exams" :key="index">
          <td>{{ exam.name }}</td>
          <td>{{ exam.aliases.map(alias => alias.value).join(", ") }}</td>
          <td class="is-capitalized">{{ exam.department.name }}</td>
          <td>{{ exam.price }}</td>
          <td>
            <a class="button is-link" @click="edit(exam)">
              <span class="icon">
                <i class="far fa-edit"></i>
              </span>
              <span>
                Edit
              </span>
            </a>
          </td>
        </tr>
      </tbody>
    </table>
    <examcreateform-component></examcreateform-component>
    <exameditform-component></exameditform-component>
  </div>
</template>

<script>
import ExamCreateFormComponent from "@/components/ExamCreateFormComponent";
import ExamEditFormComponent from "@/components/ExamEditFormComponent";

export default {
  name: "ExamListingTableComponent",
  created() {
    this.$store.dispatch(
      "fetchLabtechExams",
      this.$store.state.authentication.inMemoryToken
    );
    let store = this.$store;
    this.$socket.on("create-exam", exam => {
      store.dispatch("addCreatedExam", exam);
    });
  },
  methods: {
    edit(exam) {
      this.$store.dispatch("updateActiveExam", exam);
      this.$store.dispatch("toggleEditExamModalOn", true);
    }
  },
  computed: {
    exams() {
      return this.$store.state.labtech.exams;
    }
  },
  components: {
    "examcreateform-component": ExamCreateFormComponent,
    "exameditform-component": ExamEditFormComponent
  }
};
</script>
