import Vue from "vue";
import Router from "vue-router";
import HomepageView from "./views/Home";
import LoginView from "./views/Login";
import ReceptionistView from "./views/ReceptionistView";
import PatientRegistrationComponent from "@/components/PatientRegistrationComponent";
import ResultRenderingComponent from "@/components/ResultRenderingComponent";
import PaymentRequestListingComponent from "@/components/PaymentRequestListingComponent";
import RevenueDashboardComponent from "@/components/RevenueDashboardComponent";
import TransactionHistoryComponent from "@/components/TransactionHistoryComponent";
import CashierView from "./views/CashierView";
import LabTechView from "./views/LabTechView";
import LabTechExamRequestComponent from "@/components/LabTechExamRequestComponent";
import ExamRegistrationComponent from "@/components/ExamRegistrationComponent";
import VaccineRegistrationComponent from "@/components/VaccineRegistrationComponent";
import BiologistView from "./views/BiologistView";
import AdminView from "./views/AdminView";
import PartnerView from "./views/PartnerView";
import PartnerLabRequestsComponent from "@/components/PartnerLabRequestsComponent";
import PartnerPatientResultComponent from "@/components/PartnerPatientResultComponent";
import VaccinesDiscountComponent from "@/components/VaccinesDiscountComponent";
import ExamsDiscountComponent from "@/components/ExamsDiscountComponent";
import UsersListingComponent from "@/components/UsersListingComponent";
import DepartmentsListingComponent from "@/components/DepartmentsListingComponent";
import store from "./store/index";
import { silentRefresh } from "./helpers/authentication";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: HomepageView
    },
    {
      path: "/login",
      name: "login",
      component: LoginView
    },
    {
      path: "/:laboratoryname/receptionist/:username",
      component: ReceptionistView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      },
      children: [
        {
          path: "registration",
          component: PatientRegistrationComponent
        },
        {
          path: "results",
          component: ResultRenderingComponent
        }
      ]
    },
    {
      path: "/:laboratoryname/cashier/:username",
      name: "cashier",
      component: CashierView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      },
      children: [
        {
          path: "lab-requests",
          component: PaymentRequestListingComponent
        },
        {
          path: "transaction-history",
          component: TransactionHistoryComponent
        },
        {
          path: "exam-discounts",
          component: ExamsDiscountComponent
        },
        {
          path: "vaccine-discounts",
          component: VaccinesDiscountComponent
        }
      ]
    },
    {
      path: "/:laboratoryname/biologist/:username",
      component: BiologistView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      }
    },
    {
      path: "/:laboratoryname/labtech/:username",
      component: LabTechView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      },
      children: [
        {
          path: "exam-requests",
          component: LabTechExamRequestComponent
        },
        {
          path: "exams",
          component: ExamRegistrationComponent
        },
        {
          path: "vaccines",
          component: VaccineRegistrationComponent
        }
      ]
    },
    {
      path: "/:laboratoryname/admin/:username",
      component: AdminView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      },
      children: [
        {
          path: "users",
          component: UsersListingComponent
        },
        {
          path: "revenue-dashboard",
          component: RevenueDashboardComponent
        },
        {
          path: "departments",
          component: DepartmentsListingComponent
        }
      ]
    },
    {
      path: "/:laboratoryname/partner/:username",
      component: PartnerView,
      beforeEnter(to, from, next) {
        if (store.state.authentication.authenticated) {
          next();
        }
        silentRefresh().then(() => {
          if (store.state.authentication.authenticated) {
            next();
          } else {
            next({ path: "/login" });
          }
        });
      },
      children: [
        {
          path: "lab-requests",
          component: PartnerLabRequestsComponent
        },
        {
          path: "patient-results",
          component: PartnerPatientResultComponent
        }
      ]
    }
  ]
});
