<template>
  <div v-if="examRequest != null" class="modal" :class="{ 'is-active': isActive }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title is-capitalized">
          Customer: 
          {{examRequest.patient.first_name + " " + examRequest.patient.last_name}}
        </p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="box" style="margin-top: 1em;" v-for="(exam, key) in examRequest.exams" :key="key">
            <h1 class="title is-capitalized">{{ exam.name }}</h1>
            <h2 class="subtitle is-capitalized">{{ exam.department.name }}</h2>
            <table class="table is-fullwidth is-bordered is-hoverable">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Value</th>
                        <th>Unit</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(parameter, key) in exam.parameters" :key="key">
                        <td><span>{{ parameter.name }}</span></td>
                        <td>
                            <input 
                                class="input" 
                                type="text" placeholder="Value" 
                                v-model="parameter.value">
                        </td>
                        <td><span>{{ parameter.unit }}</span></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="box" style="padding-top: 1em; margin-bottom: 1em;">
          <div class="columns">
            <div class="column">
              <button 
                class="button is-success is-medium is-fullwidth" 
                @click="saveChanges(examRequest)">
                  Save changes
              </button>
            </div>
            <div class="column">
              <button 
                class="button is-link is-medium is-fullwidth" 
                @click="completeRegistration(examRequest)">
                  Done
              </button>
            </div>
            <div class="column">
              <button class="button is-danger is-medium  is-fullwidth" @click="closeModal">Cancel</button>
            </div>
          </div>
        </div>
      </section> 
    </div>
  </div>
</template>

<script>

export default {
    name: "ResultRegistrationComponent",
    data() {
      return {
      }
    },
    methods: {
        closeModal(){
            this.$store.dispatch("toggleResultRegistrationModalOn", false);
            this.$store.dispatch("updateSelectedRegistrationExamRequest", null);
        },
        saveChanges(examRequest){ 
          this.$socket.emit("update-labrequest", examRequest);
          this.closeModal();
        },
        completeRegistration(examRequest){
          examRequest.status = "result_registered";
          this.$socket.emit("update-labrequest", examRequest);
          this.closeModal();
        }
    },
    computed: {
        isActive() {
          return this.$store.state.labtech.isResultRegistrationModalOn;
        },
        examRequest() {
          return this.$store.state.labtech.selectedRegistrationExamRequest;
        }
    }
}
</script>