<template>
  <div>
    <searchpatientfile-component></searchpatientfile-component>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Sex</th>
          <th>Email</th>
          <th>Telephone</th>
          <th>Doctor's Name</th>
          <th>Status</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody :key="key" v-for="(examRequest, key) in examRequests">
        <tr v-show="examRequest.status == 'verified'">
          <td>{{ examRequest.patient.first_name }}</td>
          <td>{{ examRequest.patient.last_name }}</td>
          <td>{{ examRequest.patient.sex }}</td>
          <td>{{ examRequest.patient.email }}</td>
          <td>{{ examRequest.patient.telephone }}</td>
          <td>{{ examRequest.doctor.name }}</td>
          <td>{{ examRequest.status }}</td>
          <td>
            <!--a class="button is-link" href="./../../../data/medicalresults.pdf"
            >View Result</a
          -->
            <button @click="download(examRequest)" class="button is-link">
              Download Result
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import searchPatientFileComponent from "@/components/SearchPatientFileComponent.vue";
import { generateReport } from "../helpers/report";

export default {
  name: "ResultRenderingComponent",
  components: {
    "searchpatientfile-component": searchPatientFileComponent
  },
  created() {
    this.$store.dispatch(
      "fetchResults",
      this.$store.state.authentication.inMemoryToken
    );
  },
  methods: {
    download(labRequest) {
      generateReport(labRequest, this.$store.state.authentication.currentUser);
    }
  },
  computed: {
    examRequests() {
      return this.$store.state.receptionist.results;
    }
  }
};
</script>
