<template>
  <div class="modal" :class="{ 'is-active': isActive }" v-if="isActive">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Exam form</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="text"
              v-model="exam.name"
              placeholder="Exam Name"
            />
          </div>
        </div>
        <div
          class="field"
          v-for="(alias, key) in exam.aliases"
          :key="'A' + key"
        >
          <div class="control">
            <div class="level">
              <input
                class="input"
                type="text"
                v-model="alias.value"
                placeholder="Alias"
              />
              <span
                class="icon has-text-danger"
                v-if="key === 0"
                @click="removeAlias(exam)"
              >
                <i class="fas fa-minus-circle"></i>
              </span>
              <span
                class="icon has-text-link"
                v-if="key === 0"
                @click="addAlias(exam)"
              >
                <i class="fa fa-plus-circle"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="number"
              v-model.number="exam.price"
              placeholder="Price"
            />
          </div>
        </div>
        <div class="field">
          <div class="control select">
            <select v-model="exam.department">
              <option value="" disabled>Select department</option>
              <option
                v-for="(department, key) in examDepartments"
                :value="department"
                :key="key"
                >{{ department.name }}
              </option>
            </select>
          </div>
        </div>
        <div
          class="field is-horizontal"
          v-for="(parameter, key) in exam.parameters"
          :key="'P' + key"
        >
          <div class="field-body">
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.name"
                  placeholder="Exam Parameter"
                />
              </div>
            </div>
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.unit"
                  placeholder="Parameter Unit"
                />
              </div>
            </div>
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.formula"
                  placeholder="Parameter Formula"
                />
              </div>
            </div>
            <div class="field" v-if="key === 0">
              <span class="icon has-text-danger" @click="removeParameter(exam)">
                <i class="fas fa-minus-circle"></i>
              </span>
            </div>
            <div class="field" v-if="key === 0">
              <span class="icon has-text-link" @click="addParameter(exam)">
                <i class="fa fa-plus-circle" aria-hidden="true"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="" style="padding-top:1 em;">
          <button
            class="button is-link is-fullwidth is-medium"
            @click="update(exam)"
          >
            Save
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { generateID } from "../helpers/utility";

export default {
  name: "ExamEditFormComponent",
  data() {
    return {
      examDepartments: [
        { name: "parasitology" },
        { name: "virology" },
        { name: "hematology" }
      ]
    };
  },
  methods: {
    validateForm() {
      return true;
    },
    update(exam) {
      this.$socket.emit("update-exam", exam);
      this.closeModal();
    },
    populateExamModal(examData) {
      this.fields = examData;
    },
    closeModal() {
      this.$store.dispatch("updateActiveExam", null);
      this.$store.dispatch("toggleEditExamModalOn", false);
    },
    addAlias(exam) {
      exam.aliases.push({ value: "" });
    },
    removeAlias(exam) {
      if (exam.aliases.length >= 1) {
        exam.aliases.pop();
      }
    },
    removeParameter(exam) {
      if (exam.parameters.length > 1) {
        exam.parameters.pop();
      }
    },
    addParameter(exam) {
      exam.parameters.push({ name: "", unit: "", computed: "" });
    }
  },
  computed: {
    isActive() {
      return this.$store.state.labtech.isEditExamModalOn;
    },
    exam() {
      return this.$store.state.labtech.activeExam;
    }
  }
};
</script>
