<template>
    <nav class="navbar is-transparent" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
          <a class="navbar-item" href="/">
            <strong class="has-text-link">HealthStack</strong>
            <strong v-if="isLoggedIn" style="padding-left: 0.5rem; padding-right: 0.5rem;"> > </strong>
            <strong v-if="isLoggedIn" class="has-text-link">{{ hospitalName }}</strong>
          </a>

          <a
            role="button"
            class="navbar-burger burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>
        <div id="navbarBasicExample" class="navbar-menu">
          <div class="navbar-end" v-if="isLoggedIn">
            <div class="navbar-item">
              <div class="dropdown" :class="{'is-active': dropdownIsActive}" @click="toggleDropdown">
                <div class="dropdown-trigger">
                  <button class="button" aria-haspopup="true" aria-controls="dropdown-menu">
                    <span>{{ username }}</span>
                    <span class="icon is-small">
                      <i class="fas fa-angle-down" aria-hidden="true"></i>
                    </span>
                  </button>
                </div>
                <div class="dropdown-menu" id="dropdown-menu" role="menu">
                  <div class="dropdown-content">
                    <a href="#" class="dropdown-item">
                      Settings
                    </a>
                    <hr class="dropdown-divider">
                    <a class="dropdown-item" @click="logout">
                      Sign Out
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="navbar-end" v-else>
            <div class="navbar-item">
              <div class="buttons">
                <router-link to="/login" class="button">
                  <span class="icon">
                    <i class="fas fa-sign-in-alt"></i>
                  </span>
                  <span>Log in</span>
                </router-link>
                <a class="button is-link" disabled>
                    <strong>Sign Up</strong>
                </a>
              </div>
            </div>
          </div>
        </div>
      </nav>
</template>

<script>
export default {
    name: "NavbarComponent",
    data() {
      return {
        dropdownIsActive: false
      }
    },
    methods: {
      toggleDropdown() {
        this.dropdownIsActive = !this.dropdownIsActive;
      },
      logout() {
        this.$store.dispatch("logout")
      }
    },
    computed: {
      isLoggedIn() {
        return this.$store.state.authentication.authenticated;
      },
      hospitalName() {
        return this.$store.state.authentication.currentUser.hospital;
      },
      username() {
        return this.$store.state.authentication.currentUser.first_name  + " " + this.$store.state.authentication.currentUser.last_name;
      }
    }
}
</script>