<template>
  <div class="modal" :class="{ 'is-active': isActive }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Create Exam</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="text"
              v-model="fields.name"
              placeholder="Exam Name"
            />
          </div>
        </div>
        <div
          class="field"
          v-for="(alias, key) in fields.aliases"
          :key="'A' + key"
        >
          <div class="control">
            <div class="level">
              <input
                class="input"
                type="text"
                v-model="alias.value"
                placeholder="Alias"
              />
              <span
                class="icon has-text-danger"
                v-if="key === 0"
                @click="removeAliasField"
              >
                <i class="fas fa-minus-circle"></i>
              </span>
              <span
                class="icon has-text-link"
                v-if="key === 0"
                @click="addAliasField"
              >
                <i class="fa fa-plus-circle"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="number"
              v-model.number="fields.price"
              placeholder="Price"
            />
          </div>
        </div>
        <div class="field">
          <div class="control select">
            <select v-model="fields.department">
              <option value="" disabled>Select department</option>
              <option
                v-for="(department, key) in examDepartments"
                :value="department"
                :key="key"
                >{{ department.name }}
              </option>
            </select>
          </div>
        </div>
        <div
          class="field is-horizontal"
          v-for="(parameter, key) in fields.parameters"
          :key="'P' + key"
        >
          <div class="field-body">
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.name"
                  placeholder="Exam Parameter"
                />
              </div>
            </div>
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.unit"
                  placeholder="Parameter Unit"
                />
              </div>
            </div>
            <div class="field">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  v-model="parameter.formula"
                  placeholder="Parameter Formula"
                />
              </div>
            </div>
            <div class="field" v-if="key === 0">
              <span class="icon has-text-danger" @click="removeParameterField">
                <i class="fas fa-minus-circle"></i>
              </span>
            </div>
            <div class="field" v-if="key === 0">
              <span class="icon has-text-link" @click="addParameterField">
                <i class="fa fa-plus-circle" aria-hidden="true"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="" style="padding-top:1 em;">
          <button
            class="button is-link is-fullwidth is-medium"
            @click="createExam"
          >
            Create
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { generateID } from "../helpers/utility";

export default {
  name: "ExamCreateFormComponent",
  data() {
    return {
      examDepartments: [
        { name: "parasitology" },
        { name: "virology" },
        { name: "hematology" }
      ],
      fields: {
        name: "",
        aliases: [{ value: "" }],
        price: "",
        department: "",
        parameters: [{ name: "", unit: "", parameter_formula: "" }]
      }
    };
  },
  methods: {
    createExam() {
      if (this.validateForm()) {
        let exam = this.fields;
        exam.aliases = exam.aliases;
        exam.price = exam.price;
        exam.cost = exam.price;
        exam.discounts = [];
        exam.active_discounts = [];
        exam.is_complete = false;
        exam.is_sample_collected = false;
        exam.created_at = Date.now();
        exam.updated_at = Date.now();
        exam.type = "exam";
        exam.key = generateID();
        this.$socket.emit("create-exam", exam);
        this.closeModal();
      }
    },
    validateForm() {
      return true;
    },
    refreshForm() {
      this.fields = {
        name: "",
        aliases: [{ value: "" }],
        price: "",
        department: "",
        parameters: [{ name: "", unit: "", parameter_formula: "" }]
      };
    },
    closeModal() {
      this.refreshForm();
      this.$store.dispatch("toggleCreateExamModalOn", false);
    },
    addAliasField() {
      this.fields.aliases.push({ value: "" });
    },
    removeAliasField() {
      if (this.fields.aliases.length > 1) {
        this.fields.aliases.pop();
      }
    },
    removeParameterField() {
      if (this.fields.parameters.length > 1) {
        this.fields.parameters.pop();
      }
    },
    addParameterField() {
      this.fields.parameters.push({ name: "", unit: "", computed: "" });
    }
  },
  computed: {
    isActive() {
      return this.$store.state.labtech.isCreateExamModalOn;
    }
  }
};
</script>
