<template>
    <table class="table is-fullwidth">
        <thead> 
            <tr>
                <th>Time</th>
                <th>Customer</th>
                <th>Status</th>
                <th>Action</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(examRequest, key) in examRequests" :key="key">
                <td>{{ formatTime(examRequest.order_time) }}</td>
                <td class="is-capitalized">{{ examRequest.patient.first_name + " " + examRequest.patient.last_name }}</td>
                <td>{{ formatStatus(examRequest.status) }}</td>
                <td>
                    <button 
                        v-show="isPaid(examRequest.status)" 
                        @click="collectSample(examRequest)"
                        class="button is-primary is-fullwidth">
                        Collect Sample
                    </button>
                    <button 
                        v-show="isSampleCollected(examRequest.status)"
                        @click="toggleRegisterResulltsModal(examRequest)"
                        class="button is-link is-fullwidth">
                        Register Results
                    </button>
                </td>
            </tr>
            <resultregistration-component></resultregistration-component>
        </tbody>
    </table>
</template>

<script>
import ResultRegistrationComponent from "@/components/ResultRegistrationComponent";

export default {
    created() {
        this.$store.dispatch(
            "fetchLabTechLabRequests", this.$store.state.authentication.inMemoryToken
        );
        let store = this.$store;
        this.$socket.on("update-labrequest", labRequest => {
            if(labRequest.status === "paid") {
                store.dispatch("addPaidLabRequest", labRequest);
            }
        })
    },
    methods: {
        formatTime(time) {
            let date = new Date(time);
            return date.toLocaleString()
        },
        formatStatus(status) {
            switch(status) {
                case "paid":
                    return "Paid";
                    break;
                case "sample_collected":
                    return "Sample Collected";
                    break;

                default:
                    return status; 
            }
        },
        toggleRegisterResulltsModal(labRequest) {
            this.$store.dispatch("updateSelectedRegistrationExamRequest", labRequest);
            this.$store.dispatch("toggleResultRegistrationModalOn", true);
        },
        collectSample(labRequest) {
            labRequest.status = "sample_collected";
            this.$socket.emit("update-labrequest", labRequest);
        },
        isPaid(status) {
            return status === "paid";
        },
        isSampleCollected(status) {
            return status === "sample_collected";
        }
    },
    computed: {
        examRequests(){
            return this.$store.getters.getIncompleteExamRequests.sort((labRequestA, labRequestB) => {
                if(labRequestA.order_time < labRequestA.order_time) {
                    return -1;
                } else if(labRequestA.order_time > labRequestB.order_time) {
                    return 1;
                } else {
                    return 0;
                }
            });
        }
    },
    components: {
        "resultregistration-component": ResultRegistrationComponent
    }
}
</script>